import React, { useState } from "react";
import * as Accordion from "@radix-ui/react-accordion";
import { cn } from "@/utils/cn";
import { ChevronDown } from "lucide-react";

export interface SubMenuItem {
  url: string;
  title: string;
}

export interface MenuItem {
  url: string;
  title: string;
  icon?: React.ReactNode;
  subMenu?: SubMenuItem[];
}

export interface SideNavigationProps {
  title?: string;
  menus: MenuItem[];
  selectedUrl: string;
  onMenuClick: (url: string) => void;
  headerSlot?: React.ReactNode;
  className?: string;
}

interface NavigationMenuItemProps {
  menu: MenuItem;
  isOpen: boolean;
  isSelected: boolean;
  selectedUrl: string;
  onMenuClick: (url: string) => void;
}

const NavigationMenuItem = ({
  menu,
  isOpen,
  isSelected,
  selectedUrl,
  onMenuClick,
}: NavigationMenuItemProps) => {
  const isSubMenuSelected = menu.subMenu?.some(
    (sub) => sub.url === selectedUrl,
  );

  return (
    <Accordion.Item value={menu.url} className="border-none">
      <Accordion.Header className="m-0">
        <Accordion.Trigger
          onClick={(e) => {
            if (!menu.subMenu) {
              e.preventDefault();
              onMenuClick(menu.url);
            }
          }}
          className={cn(
            "border-0 group flex items-center px-5 bg-cms-gray-850",
            "text-white font-bold text-lg",
            "w-full h-15",
            "transition-colors",
            "cursor-pointer",
            !isSubMenuSelected && "data-[state=open]:bg-transparent",
            !menu.subMenu && isSelected && "bg-cms-primary-400 text-cms-black",
            isSubMenuSelected && "bg-cms-primary-200 text-cms-black",
          )}
        >
          {menu.icon && (
            <div
              className={cn(
                "mr-3 flex items-center",
                "[&>svg]:w-6 [&>svg]:h-6",
                (!menu.subMenu && isSelected) || isSubMenuSelected
                  ? "text-cms-black"
                  : "text-white",
              )}
            >
              {menu.icon}
            </div>
          )}
          <span
            className={cn(
              (!menu.subMenu && isSelected) || isSubMenuSelected
                ? "text-cms-black"
                : "text-white",
            )}
          >
            {menu.title}
          </span>
          {menu.subMenu && (
            <ChevronDown
              className={cn(
                "ml-auto transition-transform",
                (!menu.subMenu && isSelected) || isSubMenuSelected
                  ? "text-cms-black"
                  : "text-white",
                isOpen && "rotate-180",
              )}
              size={20}
            />
          )}
        </Accordion.Trigger>
      </Accordion.Header>
      {menu.subMenu && (
        <Accordion.Content
          className={cn(
            "overflow-hidden",
            "data-[state=open]:animate-accordion-down",
            "data-[state=closed]:animate-accordion-up",
          )}
        >
          {menu.subMenu.map((subItem) => {
            const subSelected = subItem.url === selectedUrl;
            return (
              <button
                key={subItem.url}
                onClick={() => onMenuClick(subItem.url)}
                className={cn(
                  "border-0 bg-transparent flex items-center",
                  "w-full h-13 px-5 pl-14",
                  "cursor-pointer",
                  "transition-colors",
                  "hover:bg-cms-gray-900",
                )}
              >
                <span
                  className={cn(
                    "text-base font-bold",
                    "transition-colors",
                    subSelected
                      ? "text-cms-primary-400 font-bold"
                      : "text-cms-white",
                  )}
                >
                  {subItem.title}
                </span>
              </button>
            );
          })}
        </Accordion.Content>
      )}
    </Accordion.Item>
  );
};

